.pagination{
    box-sizing: border-box;
	user-select: none;
    
	span{
		display:inline-block;
		position:relative;
		width:30px;
		height:30px;
		line-height:30px;
		background:#fff;
		border:1px solid #ddd;
		color:#0275d8;
		text-align:center;
		cursor:pointer;
	}
	span:hover{
	    color: #014c8c;
	    background-color: #eceeef;
	    border-color: #ddd;
	}
	.active{
	    color: #fff;
	    background-color: #0275d8;
    	border-color: #0275d8;
	}
	.active:hover{
		color: #fff;
		background-color: #0275d8;
		border-color: #0275d8;
	}
	svg{
		position:absolute;
		height:20px;
		width:20px;
		left: 50%;
	    top: 50%;
	    -webkit-transform: translate(-50%,-50%);
	    transform: translate(-50%,-50%);
	}
	.disable{
		pointer-events: none;
	}
}


